$base1: 306;
$color-main1: hsl($base1, 60%, 24%);
$color-main2: #faeaea;
$color-main3: #ffd1d1;
$gradient-bg: linear-gradient(180deg, #78006c 42.19%, rgba(255, 208, 208, 0) 100%), #fae8e8;
$ease: all 200ms cubic-bezier(0, 0, 0.44, 0.68);
$darkSoft: hsla($base1, 10%, 10%, 80%);
$lightSoft: hsla($base1, 50%, 40%, 70%);
:root {
    --color-main1: $color-main1;
    --color-main2: $color-main2;
    --color-main3: $color-main3;
}
@import url("https://fonts.googleapis.com/css2?family=Glory:wght@200;300;500;600;800&display=swap");

html {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

*,
*:before,
*:after {
    padding: 0;
    margin: 0;
    box-sizing: inherit;
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
}

body {
    font-family: "Glory";
    font-weight: 300;
}

ul li,
a {
    list-style: none;
    text-decoration: none;
}

a {
    color: $color-main2;
}

p {
    color: $color-main1;
    font-size: 1.5em;
}

h4,
h5 h6 {
    font-weight: 300;
    color: $color-main1;
}

h3 {
    font-weight: 400;
    color: $color-main1;
}
h2 {
    font-weight: 600;
    color: $color-main1;
}
h1 {
    font-size: 10em;
    font-weight: 800;
    color: $color-main1;
}
h2 {
    font-size: 4em;
}

h3 {
    font-size: 2em;
}

h4 {
    font-size: 1.75em;
}

nav li a {
    font-size: 0.75em;
    font-weight: 300;
}

img {
    width: 100%;
}

button {
    font-family: "glory";
    font-weight: 400;
    border-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 1.75rem;
    color: $color-main1;
    background: radial-gradient(129.83% 129.83% at 49.68% -52.5%, #ffe6e6 38.96%, rgba(255, 208, 208, 0) 100%),
        rgba(103, 0, 93, 0.63);
    box-shadow: 0px 6px 12px rgba(120, 0, 108, 0.24);
    text-shadow: 0px 2px 6px rgba(120, 0, 108, 0.7);
    border-radius: 16px;
    font-size: 1.25rem;
    letter-spacing: 4px;
    transition: $ease;
}

button:hover {
    cursor: pointer;
    background: radial-gradient(102.5% 102.5% at 49.68% -52.5%, #ffd0d0 0%, rgba(255, 208, 208, 0) 100%), #78006c;
    box-shadow: 0px 4px 4px rgba(120, 0, 108, 0.18);
    color: $color-main2;
}

button:disabled {
    background: radial-gradient(129.83% 129.83% at 49.68% -52.5%, #d8acac 38.96%, rgba(255, 208, 208, 0.384) 100%),
        rgba(95, 90, 93, 0.63);
    box-shadow: none;
    text-shadow: none;
    color: $darkSoft;
    opacity: 0.5;
}

button:focus {
    box-sizing: border-box;
    outline: none;
    background: radial-gradient(102.5% 102.5% at 49.68% -52.5%, #ffd0d0 0%, rgba(255, 208, 208, 0) 100%), #78006c;
    box-shadow: inset 0 0 3px 3px $lightSoft;
    transition: all 200ms;
    color: $darkSoft;
}
button:active {
    box-sizing: border-box;
    outline: none;
    background: radial-gradient(102.5% 102.5% at 49.68% -52.5%, #ffd0d0 0%, rgba(255, 208, 208, 0) 100%), #78006c;
    box-shadow: inset 0 0 3px 6px $lightSoft;
    transition: all 200ms;
    color: $lightSoft;
}

input,
textarea {
    background-color: $color-main2;
    padding: 1em;
    margin: 1em;
    outline: none;
    border: 3px solid $color-main2;
    border-radius: 12px;
    color: $color-main1;
}
input:focus,
textarea:focus,
select:focus {
    border: 3px solid $lightSoft;
    box-sizing: border-box;
    color: $color-main1;
    font-weight: 600;
    transition: all 200ms;
}

.meow-me.focused {
    box-shadow: 0 0 4px 4px $lightSoft;
    transition: 200ms all;
}

.overlay-focus {
    position: fixed; /* Sit on top of the page content */
    display: none; /* Hidden by default */
    width: 100%; /* Full width (cover the whole page) */
    height: 100%; /* Full height (cover the whole page) */
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $darkSoft; /* Black background with opacity */
    z-index: 2; /* Specify a stack order in case you're using a different order */
    transition: all 100ms linear;
}

.overlay-focus-on {
    display: block;
}

// --------- HEADER ---------//

header {
    font-size: 2em;
    position: absolute;
    width: 100%;
    z-index: 100;

    a#logo {
        margin: 1em;
        padding: 0.25em;
        border-radius: 50%;
        svg {
            fill: $color-main3;
        }
    }

    ul {
        background-color: hsla($base1, 80%, 25%, 0.5);
        box-shadow: 0 0 8px $color-main1;
        display: flex;
        justify-content: flex-start;
        height: auto;
        flex-direction: row;
        padding: 0.25em 0;

        li {
            border-radius: 0.25em;

            a {
                margin: 0 0.75em;
                display: flex;
                align-items: center;
                padding: 1.25em 0.35em;

                text-decoration: none;
                display: block;
                width: 100%;
                height: 100%;
            }
            &:hover {
                transition: all 200ms ease;
                background-color: $lightSoft;
                cursor: pointer;
                color: $color-main3;
            }
        }
    }
}

@media (max-width: 767px) {
    header {
        width: 100%;
    }
}
// --------- HERO ---------//

section {
    height: 100vh;
    width: 100%;

    &.hero {
        display: flex;
        justify-content: space-between;
        position: relative;
        flex-direction: row;
    }

    .content {
        width: 150%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        flex-direction: column;
        padding: 2em;
        z-index: 10;
    }
    &#furry {
        background-color: $color-main1;
        h1,
        p {
            color: $color-main2;
        }
        .hero-img-bgcol {
            background-color: $color-main1;
        }
    }
    &#chillin {
        background-color: $color-main2 !important;
        .hero-img-bgcol {
            background-color: $color-main2;
        }
    }

    .hero-img-bgcol {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
    }
    .image {
        z-index: 15;
        position: relative;
        overflow: hidden;
        z-index: 0;
        background-size: cover;
        width: 100%;
        height: 100%;
        background-position: right;
        background-image: url("./pictures/cat(1).webp");
    }
    &.reverse {
        flex-direction: row-reverse;
        .content {
            display: flex;
            justify-content: center;
            align-items: flex-start;
        }
        .image {
            background-position: left;
            background-image: url("./pictures/cat(2).webp");
        }
    }
}

@media (max-width: 767px) {
    section {
        &.hero {
            flex-direction: column;
            height: 100vh;
        }
        .content {
            min-height: 60%;
            align-items: flex-start;
            justify-content: flex-end;
        }
        .image {
            z-index: 10;
            height: 500px;
        }
    }
}
// --------- FACTS ---------//

section.facts {
    padding-top: 4em;
    padding-bottom: 4em;
    height: auto;
    text-align: center;
    display: block;
    position: relative;
    z-index: 0;
    background: $gradient-bg;
    h2 {
        color: $color-main2;
    }
}

.facts-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
}

.fact {
    z-index: 0;
    border: 1px solid hsla($base1, 80%, 40%, 40%);
    margin: clamp(0.5vw, 2vw, 10vw);
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    overflow: hidden;
    position: relative;
    border-radius: 16px;
    width: 100%;
    max-width: 300px;
    box-shadow: 0px -71px 40px -50px rgba(49, 0, 44, 0.24), 0px 10px 5px rgba(92, 0, 83, 0.63);
    height: auto;
    max-height: 700px;
    transition: all 200ms ease;

    h3 {
        text-transform: uppercase;
        text-shadow: 0px 4px 4px $color-main1;
        color: white;
        opacity: 0.8;
        margin: 42px 0px;
        letter-spacing: 0.27em;
        line-height: 1em;
        margin: 2em 0.5em;
    }

    p {
        text-align: left;
        font-size: 1em;
        font-weight: 600;
        line-height: min(2em, 3em, 3vw);
        margin: 1.5em;
    }
    button {
        width: 60%;
        margin-bottom: 1.5em;
        align-self: center;
        position: relative;
        display: block;
        z-index: 10;
    }

    & .background {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: -5;
        background-size: cover;
        transform: scale(1.25);
        background-position: center;
        filter: blur(6px);

        &.fact1 {
            background-image: url("./files/illustration (1).jpg");
        }
        &.fact2 {
            background-image: url("./files/illustration (2).jpg");
        }
        &.fact3 {
            background-image: url("./files/illustration (3).jpg");
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: rgba($color-main3, 0.7);
        }
    }

    &::after {
        background-color: rgba($color-main3, 0.5);
        z-index: 10;
    }
}

@media (max-width: 767px) {
    section {
        .facts-container {
            flex-direction: column;
            align-items: center;
        }
        .fact {
            max-width: 80vw;
            margin-bottom: 10vh;
        }
        .fact p {
            text-align: center;
            font-size: 1.25em;
            font-weight: 500;
            line-height: min(3em, 4em, 6vw);
        }
    }
}

// --------- PICTURES ---------//

section.pictures {
    background-color: $color-main2;
    height: 100vh;
    text-align: center;
    overflow: hidden;
    width: auto;
}
.pictures-container {
    height: auto;
    margin-top: 4em;
    display: flex;
    justify-content: flex-start;
    // overflow-x: hidden;

    .picture {
        border-radius: 4 * 8px;
        overflow: hidden;
        height: 70vh;
        min-width: clamp(250px, 50vw, 50vh);
        background-color: $color-main1;
        background-position: center;
        background-size: cover;
        margin: 0 2em;

        &[picture="1"] {
            background-image: url("pictures/cat(1).webp");
            background-position: right;
        }
        &[picture="2"] {
            background-image: url("pictures/cat(2).webp");
            background-position: left;
        }
        &[picture="3"] {
            background-image: url("pictures/cat(3).webp");
        }
        &[picture="4"] {
            background-image: url("pictures/cat(4).webp");
        }
        &[picture="5"] {
            background-image: url("pictures/cat(5).webp");
        }
        &[picture="6"] {
            background-image: url("pictures/cat(6).webp");
        }
        &[picture="7"] {
            background-image: url("pictures/cat(7).webp");
        }
        &[picture="8"] {
            background-image: url("pictures/cat(8).webp");
        }
    }
}

// --------- MEOW ME ---------//

section.meow {
    @extend .pictures;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;

    h3 {
        font-size: clamp(2em, 5vw, 3em);
    }
}

.meow-me {
    min-height: 85vh;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: stretch;
    background-color: $color-main3;
    border-radius: 8 * 4px;
    overflow: hidden;
    z-index: 100;
    h3 {
        opacity: 0.8;
    }
    form {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    input,
    textarea {
        width: 85%;
        max-height: 100px;
    }
    button[type="submit"] {
        max-width: 50%;
        margin: 1em 1em 0 auto;
    }
}
.opening-hours {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 4em;
    text-align: left;
    .day {
        margin: 0.8em 0;
        h4 {
            margin: 0.15em 0;
        }
    }
}

@media (max-width: 767px) {
    section {
        &.meow {
            flex-direction: column-reverse;
            height: auto;
        }
        .meow-me,
        .opening-hours {
            margin-bottom: 10vh;
        }
        .opening-hours {
            align-items: center;
            margin-left: 0;
        }
    }
}

// --------- FOOTER ---------//

footer {
    height: 30vh;
    background-color: $color-main1;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    padding-bottom: 3em;

    h4 {
        color: rgba($color-main3, 0.8);
        margin-bottom: 1.5em;
    }
    .container {
        display: table;
        position: relative;
        display: flex;
        justify-content: flex-start;
    }
    form {
        position: relative;
    }
    input {
        margin: 0;
        box-sizing: border-box;
        background-color: white;
        border: 3px solid $color-main3;
        width: 300px;
        height: 60px;
    }
    input:focus,
    input:selected {
        border: 3px solid $color-main2;
    }
    button {
        border-radius: 11px;
        height: 45px;
        position: absolute;
        margin: -58px 189px;
        background: radial-gradient(102.5% 102.5% at 49.68% -52.5%, #ffd0d0 0%, rgba(255, 208, 208, 0) 100%), #45c4bd;
    }
    button:hover {
        background: radial-gradient(102.5% 102.5% at 49.68% -52.5%, #ffd0d0 0%, rgba(255, 208, 208, 0) 100%), #1ba1a1;
    }
}

.social-links {
    display: flex;
    flex-direction: row;
    margin-left: 2em;

    .social-icon {
        border: 3px solid $color-main3;
        border-radius: 50%;
        width: 50px;
        height: 50px;
        margin-right: 25px;
    }
}

@media (max-width: 767px) {
    footer {
        padding: 4em;
        flex-direction: column;
        align-items: center;
        justify-content: flex-end;
        height: 100vh;

        h4 {
            text-align: center;
            font-size: 3em;
        }
        .social-links {
            margin: 20vh 0;
            .social-icon {
                margin: 0 4vw;
            }
        }
    }
}
